import React, { useState } from 'react';
import { TouchableOpacity, Text, StyleSheet } from 'react-native';
import { ConfirmDialog } from './ConfirmDialog';
import { SocketService } from '../services/socket.service';
import { useNavigation } from '@react-navigation/native';

interface LeaveGameButtonProps {
    gameId: string;
    style?: any;
}

export const LeaveGameButton: React.FC<LeaveGameButtonProps> = ({ gameId, style }) => {
    const [showConfirm, setShowConfirm] = useState(false);
    const navigation = useNavigation();
    const socketService = SocketService.getInstance();

    const handleLeaveGame = async () => {
        try {
            const response = await socketService.leaveGame(gameId);
            if (response.success) {
                navigation.reset({
                    index: 0,
                    routes: [{ name: 'Home' as never }],
                });
            } else {
                console.error('Failed to leave game');
            }
        } catch (error) {
            console.error('Error leaving game:', error);
        }
        setShowConfirm(false);
    };

    return (
        <>
            <TouchableOpacity 
                style={[styles.leaveButton, style]} 
                onPress={() => setShowConfirm(true)}
            >
                <Text style={styles.leaveButtonText}>Leave Game</Text>
            </TouchableOpacity>

            <ConfirmDialog
                visible={showConfirm}
                title="Leave Game"
                message="Are you sure you want to leave this game? This action cannot be undone."
                onConfirm={handleLeaveGame}
                onCancel={() => setShowConfirm(false)}
            />
        </>
    );
};

const styles = StyleSheet.create({
    leaveButton: {
        backgroundColor: '#d32f2f',
        padding: 10,
        borderRadius: 5,
        position: 'absolute',
        top: 10,
        right: 10,
    },
    leaveButtonText: {
        color: 'white',
        fontSize: 16,
        fontWeight: 'bold',
    },
}); 