import React, { useState } from 'react';
import { View, TextInput, Button, StyleSheet } from 'react-native';
import { SocketService } from '../services/socket.service';
import { useTheme } from '../theme/ThemeContext';


export const JoinGameScreen: React.FC<{ route: any, navigation: any }> =
    ({route, navigation}) => {
        const { colors } = useTheme();
        const { playerName } = route.params;
        const socketService = SocketService.getInstance();
        const [gameId, setGameId] = useState('');

        const handleJoinGame = async () => {
            await socketService.connect(playerName);
            await socketService.joinGame(gameId, playerName);
            navigation.navigate('Game', { gameId, playerName });
        };

        const styles = StyleSheet.create({
            container: {
                flex: 1,
                padding: 20,
                justifyContent: 'center',
            },
            input: {
                borderWidth: 1,
                borderColor: colors.inputBorder,
                padding: 10,
                marginBottom: 20,
                borderRadius: 5,
            },
            buttonContainer: {
                gap: 10,
            },
        });

        return (
            <View style={[styles.container, { backgroundColor: colors.containerBackground }]}>
                <TextInput
                    style={[styles.input, { backgroundColor: colors.inputBackground }]}
                    placeholder="Enter game name"
                    onChangeText={(text) => setGameId(text)}
                    placeholderTextColor={colors.playerStatsText}
                />
                <Button 
                    title="Join Game" 
                    onPress={handleJoinGame} 
                    color={colors.primaryButton}
                />
            </View>
        );
    }
    